import React,{useState} from 'react';
import {useHistory} from 'react-router-dom';
import Axios from 'axios';
import Footer from './footer';

const Contact = ()=>{
    const url="https://eduprov-api.herokuapp.com/api/contact";
    const history = useHistory();
    const [user, setUser]= useState({
        name:"",email:"",mobile:"",dob:"",gender:"",academic:"",
        activities:"",course:"",father:"",mother:"",occupation:"",
        caste:"",country:"",state:"",city:"",nationality:"",college:"",
        qualification:"",address:"",date:""
    });

    let name,value;

    const handleInput = (e)=>{
        console.log(e);
        name=e.target.name;
        value=e.target.value;

        setUser({...user, [name]:value});
    }

    const PostData= async (e)=>{
        e.preventDefault();
        Axios.post(url)
    

        const{ name, email, mobile, dob, gender, academic, activities, course, father, mother, occupation, caste, country, state, city, nationality, college, qualification, address, date} = user;

    const res = await  fetch("https://eduprov-api.herokuapp.com/api/contact", {
        method:"POST",
        headers:{
            "Content-Type":"application/json"
        },
        body:JSON.stringify({
            name, email, mobile, dob, gender, academic, activities, course, father, mother, occupation, caste, country, state, city, nationality, college, qualification, address, date
        })
    })

    const data = await res.json();
    if(data.status === 500 || !data){
        window.alert("Registration Failed");
        console.log("Registration Failed");
    } else{
        window.alert("Registration Success");
        console.log("Registration Success");
    }
   
    }

    return(

        
      <form method="POST"  onSubmit={PostData}>
          <div className="container mt-2">
              <div className="row">
                  <div>
                  <div className="col-lg-12">
                        <h3 className="text-center text-primary"> Student Application Form </h3>
                        <p className="text-center text-success"></p>
                    </div>
                    <div className="row">
                    <div className="col-lg-1"></div>
                    <div className="col-lg-10">
                        <div className="card">
                            <div className="card-header bg-success text-white">Enter Student Details </div>
                            <div className="card-body">
                             <div className="row form-group">
                                 <div className="col-lg-4 mt-2">
                                     <label>Full Name</label>
                                 <input type="text" className="form-control" name="name" id="name"
                                    onClick={PostData}
                                    value={user.name} 
                                    onChange={handleInput}
                                    />
                                 </div>
                                 <div className="col-lg-4 mt-2">
                                     <label>Email-ID</label>
                                 <input type="text" className="form-control" name="email" id="email"
                                    value={user.email} 
                                    onChange={handleInput}
                                    />
                                 </div>
                                 <div className="col-lg-4 mt-2">
                                     <label>Mobile No</label>
                                 <input type="number" className="form-control" name="mobile" id="mobile"
                                    value={user.mobile} 
                                    onChange={handleInput}
                                    />
                                 </div>
                                 <div className="col-lg-4 mt-2">
                                     <label>Dob</label>
                                 <input type="text" className="form-control" name="dob" id="dob"
                                    value={user.dob} 
                                    onChange={handleInput}
                                    />
                                 </div>
                                 <div className="col-lg-4 mt-2">
                                     <label>Gender</label>
                                 <input type="text" className="form-control" name="gender" id="gender"
                                    value={user.gender} 
                                    onChange={handleInput}
                                    />
                                 </div>
                                 <div className="col-lg-4 mt-2">
                                     <label>Academics</label>
                                 <input type="text" className="form-control" name="academic" id="academic"
                                    value={user.academic} 
                                    onChange={handleInput}
                                    />
                                 </div>
                                 <div className="col-lg-4 mt-2">
                                     <label>Activities</label>
                                 <input type="text" className="form-control" name="activities" id="activities"
                                    value={user.activities} 
                                    onChange={handleInput}
                                    />
                                 </div>
                                 <div className="col-lg-4 mt-2">
                                     <label>Course</label>
                                 <input type="text" className="form-control" name="course" id="course"
                                    value={user.course} 
                                    onChange={handleInput}
                                    />
                                 </div>
                                 <div className="col-lg-4 mt-2">
                                     <label>Father Name</label>
                                 <input type="text" className="form-control" name="father" id="father"
                                    value={user.father} 
                                    onChange={handleInput}
                                    />
                                 </div>
                                 <div className="col-lg-4 mt-2">
                                     <label>Mother Name</label>
                                 <input type="text" className="form-control" name="mother" id="mother"
                                    value={user.mother} 
                                    onChange={handleInput}
                                    />
                                 </div>
                                 <div className="col-lg-4 mt-2">
                                     <label>Parents Occupation</label>
                                 <input type="text" className="form-control" name="occupation" id="occupation"
                                    value={user.occupation} 
                                    onChange={handleInput}
                                    />
                                 </div>
                                 <div className="col-lg-4 mt-2">
                                     <label>Caste</label>
                                 <input type="text" className="form-control" name="caste" id="caste"
                                    value={user.caste} 
                                    onChange={handleInput}
                                    />
                                 </div>
                                 <div className="col-lg-4 mt-2">
                                     <label>Country</label>
                                 <input type="text" className="form-control" name="country" id="country"
                                    value={user.country} 
                                    onChange={handleInput}
                                    />
                                 </div>
                                 <div className="col-lg-4 mt-2">
                                     <label>State</label>
                                 <input type="text" className="form-control" name="state" id="state"
                                    value={user.state} 
                                    onChange={handleInput}
                                    />
                                 </div>
                                 <div className="col-lg-4 mt-2">
                                     <label>City</label>
                                 <input type="text" className="form-control" name="city" id="city"
                                    value={user.city} 
                                    onChange={handleInput}
                                    />
                                 </div>
                                 <div className="col-lg-4 mt-2">
                                     <label>Nationality</label>
                                 <input type="text" className="form-control" name="nationality" id="nationality"
                                    value={user.nationality} 
                                    onChange={handleInput}
                                    />
                                 </div>
                                 <div className="col-lg-4 mt-2">
                                     <label>Prefered College</label>
                                 <input type="text" className="form-control" name="college" id="college"
                                    value={user.college} 
                                    onChange={handleInput}
                                    />
                                 </div>
                                 <div className="col-lg-4 mt-2">
                                     <label>Qualification</label>
                                 <input type="text" className="form-control" name="qualification" id="qualification"
                                    value={user.qualification} 
                                    onChange={handleInput}
                                    />
                                 </div>
                                 <div className="col-lg-4 mt-2">
                                     <label>Address</label>
                                 <input type="text" className="form-control" name="address" id="address"
                                    value={user.address} 
                                    onChange={handleInput}
                                    />
                                 </div>
                                 <div className="col-lg-4 mt-2">
                                     <label>Application Date</label>
                                 <input type="text" className="form-control" name="date" id="date"
                                    value={user.date} 
                                    onChange={handleInput}
                                    />
                                 </div>
                                 
                             </div>
                             <br/>
                             <div className="text-center">
                             <button  className="btn btn-success btn-block" >Send Message</button>
                             </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-1"></div>
                    </div>
                  </div>
              </div>
              <br/>
              <Footer/>
          </div>
      </form>
    )
}
export default Contact;